import React, { useState } from 'react';

import { Meta } from '@storybook/react';

import { Button } from './Button';

import styled from 'styled-components';

export default {
    component: Button,
    title: 'Atoms/Button',
} as Meta;

const ComponentBox = styled.div`
    display: flex;
    justify-content: space-around;
`;

export const Default: React.VFC<{}> = () => {
  
    const [borderRadius, setBorderRadius] = useState('0%');


    return (<>
        <>
            <h5>Solid button</h5>
            <ComponentBox>
                <Button borderRadius={borderRadius}
                    label="Solid Button" />
                <Button borderRadius={borderRadius}
                    className="button_hover"
                    label="Solid Button" />
            </ComponentBox>
        </>
        <>
            <h5>Ghost button</h5>
            <ComponentBox>
              <Button borderRadius={borderRadius}
                  class
                  label="Ghost Button" />
              <Button borderRadius={borderRadius}
                  className="button_hover"
                  label="Ghost Button" />
          </ComponentBox>
        </>
        <>
            <h5>Outline button</h5>
            <Button borderRadius={borderRadius} label="Button" />
        </>
        <>
            <h5>Light Solid button</h5>
            <Button borderRadius={borderRadius} label="Button" />
        </>
        <>
            <h5>Text button</h5>
            <Button borderRadius={borderRadius} label="Button" />
        </>
        <>
            <h5>Icon button</h5>
            <Button borderRadius={borderRadius} label="Button" />
        </>
        <>
            <h5>Destructive button</h5>
            <Button borderRadius={borderRadius} label="Button" />
        </>
        
    </>);
};;
    
export const Danger: React.VFC<{}> = () => <Button background="#ff0" label="Button" />;
Default.storyName = '';





